import Vue from 'vue'
import VueRouter from 'vue-router'
import Accueil from '../components/accueil/accueil.vue'
import Formations from '../components/formations/formations.vue'
import Ingenieur from '../components/formations/ingenieur/ingenieur.vue'
import Dut from '../components/formations/dut/dut.vue'
import Experiences from '../components/experiences/experiences.vue'
import Contact from '../components/contact/contact.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Accueil',
    component: Accueil
  },
  {
    path: '/formations',
    name: 'Formations',
    component: Formations
  },
  {
    path: '/ingenieur',
    name: 'Ingenieur',
    component: Ingenieur,
    meta: { transitionName: 'slide'}
  },
  {
    path: '/dut',
    name: 'Dut',
    component: Dut,
    meta: { transitionName: 'slide'}
  },
  {
    path: '/experiences',
    name: 'Experiences',
    component: Experiences
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
