<template src="./accueil.html"></template>
<style scoped src="./accueil.css"></style>
<style scoped src="./deco.css"></style>
<style scoped src="./text_deco.css"></style>
<style>
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
</style>

<script src="https://cdnjs.cloudflare.com/ajax/libs/parallax/3.1.0/parallax.min.js"></script>

<script type="text/javascript">
import $ from 'jquery';

export default {
    mounted(){
        window.addEventListener('wheel', function(e){
            if(e.deltaY > 0){
                document.getElementById('line').classList.add('left');
                document.getElementById('container').classList.add('slide');
            }
            else {
                document.getElementById('line').classList.remove('left');
                document.getElementById('container').classList.remove('slide');
            }
        }),
        $(document).mousemove(function(event) {
            this.windowWidth = $(window).width();
            this.windowHeight = $(window).height();
            
            mouseXpercentage = Math.round(event.pageX / windowWidth * 100);
            mouseYpercentage = Math.round(event.pageY / windowHeight * 100);
            
            $('body').css('background', 'linear-gradient('+mouseXpercentage+', rgba(199,167,172,1) 0%, rgba(156,124,129,1) 100%)');
        });
    }
}
</script>